import {Component, OnInit} from '@angular/core';
import {ProfileService} from '../shared';
import {EndpointInterface, FidjService} from 'fidj-angular';
import {environment} from '../../environments/environment';

@Component({
    selector: 'app-profile',
    templateUrl: 'profile.page.html',
    styleUrls: ['profile.page.scss'],
})
export class ProfilePage implements OnInit {
    public appVersion = environment.version;
    public errors = [];
    public results = [];
    public apiVersion = 'NA';
    public apiUrl = 'NA';
    public roles: string[] = [];
    public email: string;

    private endpointInterfaces: EndpointInterface[];

    constructor(
        private profile: ProfileService,
        private fidjService: FidjService
    ) {}

    get defaultEndpointUrl() {
        return this.profile.defaultUrlForAPI;
    }

    get endpointUrl() {
        return this.endpointInterfaces && this.endpointInterfaces.length > 0
            ? this.endpointInterfaces[0]
            : null;
    }

    ngOnInit() {
        this.refresh();
    }

    async refresh() {
        try {
            await this.profile.refreshProfile();
        } catch (e) {
            console.error('refresh pb:', e);
        }

        if (!this.profile.isLoggedIn()) {
            return this.profile.gotoLout();
        }

        this.endpointInterfaces = await this.fidjService.getEndpoints();
        this.roles = await this.fidjService.getRoles();
        this.email = this.profile.getEmail();
    }
}
